import axios from 'axios'
import _ from 'lodash';

const state = {
  weather: []
}

const getters = {
  getAllWeather(state){
    return state.weather
  }
}

const mutations = {
  ajaxWeather(state, city){    
    const API_KEY = '2cee145031315bc95bdd74f4a88e9b68';
      // call openweathermap api
    // const ROOT_URL = `http://api.openweathermap.org/data/2.5/forecast?appid=${API_KEY}`;

    const ROOT_URL = `https://cors-anywhere.herokuapp.com/http://api.openweathermap.org/data/2.5/forecast?appid=${API_KEY}`
    const url = `${ROOT_URL}&q=${city},tw`;
    
    axios.get(url).then(
      result => {
        // console.log(result);
        let isExist = false
        _.forEach(state.weather,(value)=>{
          // 如果之前已經查詢過
          let cityName = value.data.city.name
          // 如果判斷有相同的就跳出迴圈
          if(_.isEqual(_.toUpper(city),_.toUpper(cityName))){
            isExist = true
            return false
          }
        })
        
        // 沒有重複才塞
        if(!isExist){
          // state.weather = [...state.weather, result];
          // console.log([].concat([result]));
          state.weather = state.weather.concat([result])
          // console.log(state.weather);
        }

        // 先只做只有一筆
        // state.weather = []
        // console.log("1:",state.weather);
        // state.weather = state.weather.concat([result])
        // console.log("2:",state.weather);
      }
    );
  }
}

const actions = {
  ajaxWeather({commit}, city){
    commit('ajaxWeather', city);
  }
}

export default {
	state,
	getters,
	mutations,
	actions,
	namespaced: true
}


