<template>
  <div id="app">
    <h1>{{magTitle}}</h1>
    <div class="container">
      <div class="col-xs-12 col-sm-12">
        <div class="input-group">
          <input ref="city" type="text" class="form-control" placeholder="請輸入City名稱">
          <span class="input-group-btn">
            <button class="btn btn-info" type="button" @click="searchWeather">搜尋</button>
          </span>
        </div>
      </div>
    </div>
  
    <div class="container">
      <div class="row" v-for="(weather, index) in getAllWeather" :key="index">
        <div class="col-xs-12 col-sm-3">
          <!-- <skyline /> -->
          <googleMap :city="weather.data.city.name" :lat="weather.data.city.coord.lat" :lon="weather.data.city.coord.lon" />
          <!-- <h3>{{weather.data.city.name}}</h3> -->
        </div>
        <div class="col-xs-12 col-sm-3">
          <!-- {{getTemps(weather.data.list)}} -->
          <commitChart :color="'#f87979'" :temps="getTemps(weather.data.list)" :label="'Temps °C'" :labels="getDateList(weather.data.list)" />
          <!-- <skyline :temps="getTemps(weather.data.list)"/> -->
          <!-- <h3>Temps °C</h3> -->
        </div>
        <div class="col-xs-12 col-sm-3">
          <!-- {{weatherObj.temp}} -->
          <commitChart :color="'#b9f7d5'" :temps="getPressure(weather.data.list)" :label="'Pressure hPa'" :labels="getDateList(weather.data.list)" />
          <!-- <skyline :temps="getPressure(weather.data.list)"/> -->
          <!-- <h3>{{getPressure(weather.data.list.main.pressure)}}</h3> -->
          <!-- <h3>Pressure hPa</h3> -->
        </div>
        <div class="col-xs-12 col-sm-3">
          <commitChart :color="'#a3ccff'" :temps="getHumidity(weather.data.list)" :label="'Humidity %'" :labels="getDateList(weather.data.list)" />
          <!-- <skyline /> -->
          <!-- <h3>{{getHumidity(weather.data.list.main.humidity)}}</h3> -->
          <!-- <h3>Humidity %</h3> -->
        </div>
      </div>
    </div>
  </div>
</template>





<script>
import _ from 'lodash'
import { mapActions, mapGetters } from 'vuex'
// import skyline from './components/skyline.vue'
// import commitChart from './components/CommitChart'
// import commitChart from './components/CommitChart'
const commitChart = () => import('./components/CommitChart')
const googleMap = () => import('./components/googleMap')
export default {

  name: 'app',
  data() {
    return {
      magTitle: 'Taiwan Weather Search',
      weatherObj: {
        temp: [100, 200, 300],
        pressure: {},
        humidity: {}
      }
    }
  },
  methods: {
    getDateList(list) {

      const date = list.map((txt) => {
        return (
          txt.dt_txt
        )
      })
      return date.slice(0, 10)
    },
    getWe() {
      this.weatherObj.temp = [900, 200, 100]
      // console.log(this.getAllWeather);
    },
    searchWeather() {
      // 利用ref取得input的value
      const city = this.$refs.city.value
      if (_.isEmpty(_.trim(city))) {
        alert('不可空值')
        return false
      }

      this.ajaxWeather(city);

      // 查詢後清空
      this.$refs.city.value = ''
    },
    getTemps(tempsList) {
      const pressure = tempsList.map((weather) => {
        return (
          weather.main.temp - 273.15
        )
      })

      return pressure.slice(0, 10)
      // return pressure
    },
    getPressure(pressureList) {
      let i = 0;
      const pressure = pressureList.map((weather) => {
        return (
          weather.main.pressure
        )
      })

      return pressure.slice(0, 10)
    },
    getHumidity(humidityList) {
      let i = 0;
      const pressure = humidityList.map((weather) => {
        return (
          weather.main.humidity
        )
      })

      return pressure.slice(0, 10)
    },
    ...mapActions('weather', ['ajaxWeather'])
  },
  computed: {
    // 取出store
    ...mapGetters('weather', ['getAllWeather'])
  },
  components: {
    commitChart,
    googleMap
  },
}
</script>

<style lang="scss">
h1,
h3 {
  text-align: center;
}

.row {
  margin-bottom: 20px;
}
</style>



